* {
    margin: 0;
    box-sizing: border-box;
    padding: 0
}

body {
    background-color: #b949f5;
    height: 100vh;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

#contact-form {
    display: flex;
    flex-direction: column;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
    height: 50vh;
    width: 50vw;
    border: 2px dashed black;
    .field-message {
        width: 500px;
        height: 120px;
    }
}

.field-name,
.field-email,
.field-service,
.field-message,
.field-finish {
    display: flex;
    justify-content: space-between;
    text-align:  center;
    align-items: center;
    border-radius: 5px;
    background: #fff;
    height: 50px;
    width: 400px;
}

.field-name i,
.field-email i,
.field-service i,
.field-message i,
.field-finish i {
    padding: 10px;
    cursor: pointer;
}

.field-name input,
.field-email input,
.field-service input,
.field-message input {
    background: none;
    border: none;
    flex: 1;
    height: 100%;
    outline: none;
}

div.innactive {
    opacity: 0;
    pointer-events: none;
    transform: translate(-50%, 50%);
}

div.active {
    opacity: 1;
    pointer-events: all;
    transform: translate(-50%, -50%);
}

@keyframes shake{
    0%{
        transform: rotate(0deg);
    }
    50%{
        transform: rotate(2deg);
    }
    100%{
        transform: rotate(0deg);
    }
}

